.top {
  border-radius: 25px 25px 0 0;
  height: 100px;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
}
.circle-img {
  position: relative;
  border-radius: 50%;
  margin: auto;
  border: 7px solid #46ca32;
  /* background:  warning: gradient uses a rotation that is not supported by CSS and may not behave as expected; */

  width: 155px;
  height: 155px;
}

.name {
  position: relative;
  width: 115px;
  height: 47px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 30px;
  line-height: 47px;
  color: #ffffff;
  margin: 8px auto;
  padding-top: 10px;
  margin-top: 0px;
  text-align: center;
  z-index: 2;
  /* font-size: 20px; */
}

.designation{
  margin-top: 10px;
  text-align: center;
}

.social-icon {
  display: inline-block;
  position: relative;
  top: 35px;
  left: -6%;
  width: 600px;
  margin: 3px 18%;
}
.icon-div {
  position: relative;
  left: -41px;
  width: 246px;

  margin: 0px 3%;
  display: inline;
}
.social-icon a {
  width: 54px;
  height: 54px;
  background: rgba(217, 217, 217, 0.1);
  display: inline-flex;
  border-radius: 50%;
  margin-right: 6px;
  align-items: center;
  justify-content: center;
  line-height: 1;

  border: 1px solid rgba(255, 255, 255, 0.5);
}
.social-icon a::before {
  content: "";
  width: 54px;
  height: 54px;
  position: absolute;
  background-color: #ffffff;
  border-radius: 50%;
  transform: scale(0);
  transition: 0.3s ease-in-out;
}
.social-icon a:hover::before {
  transform: scale(1);
}
.social-icon a img {
  width: 55%;
  z-index: 1;
  transition: 0.3s ease-in-out;
}
.social-icon a:hover img {
  filter: brightness(0) saturate(100%) invert(0%) sepia(7%) saturate(98%)
    hue-rotate(346deg) brightness(98%) contrast(86%);
}

.card {
  height: 380px;
  right: 33px;
  left: -40px;
  width: 300px;
}
